export default {
	data() {
		return {
			message: '',
			reminNum: 200
		}
	},
	props:['tipMess'],
	methods: {
		clearText() {
			this.message= ""
		},
		
		onTextChange() {
			this.reminNum = 200 - this.message.length
		},
		
		getMessage() {
			this.$emit("getMess", this.message)
		}
	},
}
